.footerflex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footerflex .footer-brand {
  font-weight: bold;
  font-size: 1.9rem;
  color: white;
  display: flex;
  flex-direction: column;
  max-width: 40rem;
}
.footerflex .footer-brand .subtitle {
