}
.copyright ul li {
  display: inline-block;
  margin-left: 1rem;
}
.copyright ul li:hover {
  text-decoration: underline;
  color: rgb(123, 123, 126);
}

.menu-list {
  list-style: none;
  position: absolute;
  width: 11rem;
  transform: scale(0);
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 10px;
  overflow: hidden;
  transition: 200ms ease-in-out;
  transform-origin: top left;
  z-index: 9999;
  margin-top: 2rem;
}
.menu-list.right {
  transform-origin: top right !important;
  right: 0;
}
.menu-list.active {
  transform: scale(1);
}
.menu-list li {
  margin-inline: 0 !important;
  border-bottom: 1px solid #ccc;
  padding-inline: 1rem;
  padding-block: 0.7rem !important;
  font-weight: 500 !important;
  width: 100%;
  transition: 350ms;
}
.menu-list li:last-child {
  border: none;
}
.menu-list li:hover {
  background-color: rgba(86, 86, 86, 0.7);
}
.menu-list li:hover a {
  color: white;
}
.menu-list li.active {
  background-color: #333333;
}
.menu-list li.active a {
  color: white;
  font-weight: bold !important;
}

span.ripple {
  width: 1rem;
  aspect-ratio: 1/1;
  transform: scale(0);
  border-radius: 50%;
  position: absolute;
  animation: large 600ms linear;
  background-color: var(--color);
  opacity: 0.7;
}

@keyframes large {
  to {
    transform: scale(var(--size));
    opacity: 0;
  }
}
.d-none-large {
  display: var(--display, none) !important;
}
@media (max-width: 960px) {
  .d-none-large {
    --display:inline-block ;
  }
}

.backdrop {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.6);
  transition: 350ms;
  visibility: hidden;
  z-index: 9997;
  opacity: 0;
}
.backdrop.active {
  opacity: 0.5;
  visibility: visible;
}

