.accordion .accordion-head {
  background: white;
  border-bottom: 1px solid #ccc;
  border-top: 1px solid #ccc;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.2em 1.6em;
  font-weight: bold;
  font-size: 1.3rem;
  transition: 150ms;
}
.accordion .accordion-head:hover {
  background-color: #76787a;
  color: white;
}
.accordion .accordion-head.active {
  background-color: #565656;
  color: white;
}
.accordion .accordion-head.active i {
  transform: rotate(180deg);
}
.accordion .accordion-body {
  max-height: 0;
  overflow: hidden;
  box-sizing: border-box;
  transition: 150ms ease-in-out;
  padding: 0 1.6em;
}
.accordion .accordion-body.active {
  padding: 1.2em 1.6em;
  max-height: 200px;
}
.accordion .accordion-body .accordion-item {
  width: 100%;
  padding: 1rem 1.5rem;
  font-family: "Poppins";
  font-weight: 600;
  transition: 350ms;
}
.accordion .accordion-body .accordion-item:not(:last-child) {
  border-bottom: 1px solid #ccc;
}
.accordion .accordion-body .accordion-item:hover, .accordion .accordion-body .accordion-item.active {
  background-color: #565656;
  color: white;
}

