.sidebar {
  width: 330px;
  height: 100vh;
  position: fixed;
  top: 10px;
  transition: 500ms ease-in-out;
  margin-top: 4.5rem;
  box-shadow: 3px 0 10px 1px #b3b3b3;
  background-color: white;
  transform: translateX(-100%);
  z-index: 9998;
}
@media (max-height: 862px) {
  .sidebar {
    overflow-y: auto;
    padding-block-end: 10rem;
  }
}
.sidebar.active {
  transform: translateX(0) !important;
}
.sidebar .sidebar-item {
  border-top: 1px solid #ccc;
  padding: 1.7rem 2rem;
}
.sidebar .sidebar-item .box {
  padding: 1rem;
  color: white;
}
.sidebar .sidebar-item .box .text {
  margin-left: 3rem;
  text-align: justify;
  position: relative;
  z-index: 2;
}
.sidebar .sidebar-item .box .title {
  position: relative;
  z-index: 2;
  font-weight: bold;
  font-size: 20px;
}
.sidebar .sidebar-item .box .title .iconbox {
  background-color: #ff8a4a;
  width: fit-content;
  padding: 0.6rem;
  aspect-ratio: 1/1;
  border-radius: 5px;
  font-size: 20px;
  display: inline-block;
  margin-right: 0.6rem;
}
.sidebar .sidebar-item .box.black {
  background-color: #333333;
  position: relative;
}
.sidebar .sidebar-item .box.blue {
  background-color: rgba(43, 213, 235, 0.6588235294);
  position: absolute;
  inset: 0;
  clip-path: circle(35% at 0 85%);
  z-index: 1;
}
.sidebar .sidebar-item .box.orange {
  background-color: #565656;
