}

@font-face {
  font-family: TTCommons;
  src: url(../static/regular.woff);
  font-weight:500;
  font-style: normal;
}

@font-face {
  font-family: TTCommons;
  src: url(../static/semibold.woff);
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: TTCommons;
  src: url(../static/bold.woff);
  font-weight: 700;
  font-style: normal;
}
*,
*:after,
*:before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*:not(.font-inherit) {
  font-family: TTCommons;
}

@media (max-width: 600px) {
  :root {
    font-size: 12px;
  }
}
a {
  display: contents;
  all: unset;
}

:root {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

.sidebar {
  box-shadow: 0 2px 10px 0.5px black !important;
}

.navbar.border-1 {
  box-shadow: 0 2px 10px 0.5px black !important;
}

::-webkit-scrollbar-track {
  background: #000000 !important;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background: rgb(205, 203, 203);
}

::-webkit-scrollbar-thumb:hover {
  background: rgb(171, 171, 171);
}

section {
  background-color: var(--color);
  min-height: clamp(300px, 100vh, var(--max-height, 880px));
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  gap: 1rem;
}
section .container {
  width: min(80rem, 90vw);
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  row-gap: 1rem;
}
section .container .nav-button {
  padding: 0.6rem 1rem;
  font-family: "Poppins";
  font-weight: bold;
  background-color: #565656;
  text-decoration: none;
  color: white;
  border: none;
  border-radius: 5px;
}
section .container .nav-button:active {
  background-color: #232323;
}
section .container .nav-button:hover {
  background-color: #3d3d3d;
}
section .container.no-gap {
  row-gap: 0.3rem;
}
section .container.fluid {
  width: min(100rem, 95vw);
}
section .container.large-gap {
  row-gap: 3rem;
}
section .container h1 {
  color: var(--text-color, black);
  font-size: 5rem;
}
section .container .lead {
  color: var(--text-color, black);
  font-size: 1.1rem;
  font-weight: 300;
  font-family: "Poppins";
}
section.circle-bottom::after {
  content: "";
  width: 100%;
  height: var(--radius, 150px);
  display: block;
  position: absolute;
  bottom: -1px;
  background: radial-gradient(100vw 100% at bottom, var(--next-color, white) 0, var(--next-color, white) 55%, transparent calc(55% + 5px), transparent);
}
section.circle-top, section.cutting-edge {
  padding-bottom: 15rem;
}
section.circle-top::after {
  content: "";
  width: 100%;
  height: var(--radius, 150px);
  display: block;
  position: absolute;
  bottom: -1px;
  background: radial-gradient(100vw 100% at top, transparent 0, transparent 55%, var(--next-color, white) calc(55% + 5px), var(--next-color, white));
}
section.cutting-edge::after {
  content: "";
  width: 100%;
  height: var(--radius, 150px);
  display: block;
  position: absolute;
  bottom: -1px;
  background: linear-gradient(to left top,var(--next-color, white) 0 ,  var(--next-color, white) 50%, transparent 50.5%, transparent);
}
